<template>
  <div class="profile">
    <el-row :gutter="20">
      <!-- 个人信息卡片 -->
      <el-col :xs="24" :lg="8">
        <el-card class="profile-card">
          <div class="profile-header">
            <el-avatar :size="80" :src="userInfo.avatar">
              {{ userInfo.username?.charAt(0).toUpperCase() }}
            </el-avatar>
            <h3>{{ userInfo.username }}</h3>
            <p>{{ userInfo.email }}</p>
            <div class="profile-roles">
              <el-tag
                v-for="role in userInfo.roles"
                :key="role.id"
                type="primary"
                size="small"
              >
                {{ role.name }}
              </el-tag>
            </div>
          </div>
          
          <div class="profile-stats">
            <div class="stat-item">
              <div class="stat-value">{{ stats.recordCount }}</div>
              <div class="stat-label">创建记录</div>
            </div>
            <div class="stat-item">
              <div class="stat-value">{{ stats.fileCount }}</div>
              <div class="stat-label">上传文件</div>
            </div>
            <div class="stat-item">
              <div class="stat-value">{{ stats.loginCount }}</div>
              <div class="stat-label">登录次数</div>
            </div>
          </div>
        </el-card>
      </el-col>
      
      <!-- 编辑表单 -->
      <el-col :xs="24" :lg="16">
        <el-card title="个人信息">
          <el-tabs v-model="activeTab">
            <!-- 基本信息 -->
            <el-tab-pane label="基本信息" name="basic">
              <el-form
                ref="basicFormRef"
                :model="basicForm"
                :rules="basicRules"
                label-width="100px"
              >
                <el-form-item label="用户名" prop="username">
                  <el-input
                    v-model="basicForm.username"
                    placeholder="请输入用户名"
                    disabled
                  />
                  <div class="form-tip">用户名不可修改</div>
                </el-form-item>
                
                <el-form-item label="邮箱" prop="email">
                  <el-input
                    v-model="basicForm.email"
                    placeholder="请输入邮箱"
                  />
                </el-form-item>
                
                <el-form-item label="头像">
                  <el-upload
                    :action="uploadAction"
                    :headers="uploadHeaders"
                    :on-success="handleAvatarSuccess"
                    :on-error="handleAvatarError"
                    :before-upload="beforeAvatarUpload"
                    :show-file-list="false"
                    accept="image/*"
                  >
                    <el-avatar :size="60" :src="basicForm.avatar">
                      {{ basicForm.username?.charAt(0).toUpperCase() }}
                    </el-avatar>
                    <div class="avatar-tip">点击更换头像</div>
                  </el-upload>
                </el-form-item>
                
                <el-form-item>
                  <el-button
                    type="primary"
                    :loading="saving"
                    @click="handleUpdateBasic"
                  >
                    保存修改
                  </el-button>
                </el-form-item>
              </el-form>
            </el-tab-pane>
            
            <!-- 修改密码 -->
            <el-tab-pane label="修改密码" name="password">
              <el-form
                ref="passwordFormRef"
                :model="passwordForm"
                :rules="passwordRules"
                label-width="100px"
              >
                <el-form-item label="当前密码" prop="oldPassword">
                  <el-input
                    v-model="passwordForm.oldPassword"
                    type="password"
                    placeholder="请输入当前密码"
                    show-password
                  />
                </el-form-item>
                
                <el-form-item label="新密码" prop="newPassword">
                  <el-input
                    v-model="passwordForm.newPassword"
                    type="password"
                    placeholder="请输入新密码"
                    show-password
                  />
                </el-form-item>
                
                <el-form-item label="确认密码" prop="confirmPassword">
                  <el-input
                    v-model="passwordForm.confirmPassword"
                    type="password"
                    placeholder="请确认新密码"
                    show-password
                  />
                </el-form-item>
                
                <el-form-item>
                  <el-button
                    type="primary"
                    :loading="saving"
                    @click="handleChangePassword"
                  >
                    修改密码
                  </el-button>
                </el-form-item>
              </el-form>
            </el-tab-pane>
            
            <!-- 安全设置 -->
            <el-tab-pane label="安全设置" name="security">
              <div class="security-settings">
                <div class="security-item">
                  <div class="security-info">
                    <h4>登录保护</h4>
                    <p>开启后需要验证身份才能登录</p>
                  </div>
                  <el-switch
                    v-model="securitySettings.loginProtection"
                    @change="handleSecurityChange"
                  />
                </div>
                
                <div class="security-item">
                  <div class="security-info">
                    <h4>操作日志</h4>
                    <p>记录您的所有操作行为</p>
                  </div>
                  <el-switch
                    v-model="securitySettings.operationLog"
                    @change="handleSecurityChange"
                  />
                </div>
                
                <div class="security-item">
                  <div class="security-info">
                    <h4>最后登录时间</h4>
                    <p>{{ formatTime(userInfo.lastLogin) }}</p>
                  </div>
                </div>
              </div>
            </el-tab-pane>
          </el-tabs>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, computed, onMounted } from 'vue'
import { ElMessage, type FormInstance, type FormRules } from 'element-plus'
import { useAuthStore } from '@/stores/auth'
import { http } from '@/utils/request'
import { API_ENDPOINTS } from '@/config/api'
import type { User } from '@/types'
import dayjs from 'dayjs'

const authStore = useAuthStore()

// 响应式数据
const activeTab = ref('basic')
const saving = ref(false)
const basicFormRef = ref<FormInstance>()
const passwordFormRef = ref<FormInstance>()

const userInfo = ref<User>({
  id: 0,
  username: '',
  email: '',
  avatar: '',
  roles: [],
  createdAt: '',
  updatedAt: ''
})

const stats = ref({
  recordCount: 0,
  fileCount: 0,
  loginCount: 0
})

// 基本信息表单
const basicForm = reactive({
  username: '',
  email: '',
  avatar: ''
})

// 密码表单
const passwordForm = reactive({
  oldPassword: '',
  newPassword: '',
  confirmPassword: ''
})

// 安全设置
const securitySettings = reactive({
  loginProtection: false,
  operationLog: true
})

// 计算属性
const uploadAction = computed(() => API_ENDPOINTS.FILES.UPLOAD)
const uploadHeaders = computed(() => ({
  Authorization: `Bearer ${authStore.token}`
}))

// 验证规则
const basicRules: FormRules = {
  email: [
    { required: true, message: '请输入邮箱', trigger: 'blur' },
    { type: 'email', message: '请输入正确的邮箱格式', trigger: 'blur' }
  ]
}

const validateConfirmPassword = (rule: any, value: string, callback: any) => {
  if (value !== passwordForm.newPassword) {
    callback(new Error('两次输入的密码不一致'))
  } else {
    callback()
  }
}

const passwordRules: FormRules = {
  oldPassword: [
    { required: true, message: '请输入当前密码', trigger: 'blur' }
  ],
  newPassword: [
    { required: true, message: '请输入新密码', trigger: 'blur' },
    { min: 6, max: 50, message: '密码长度在 6 到 50 个字符', trigger: 'blur' }
  ],
  confirmPassword: [
    { required: true, message: '请确认新密码', trigger: 'blur' },
    { validator: validateConfirmPassword, trigger: 'blur' }
  ]
}

// 方法
const formatTime = (time?: string) => {
  if (!time) return '从未登录'
  return dayjs(time).format('YYYY-MM-DD HH:mm:ss')
}

// 加载用户信息
const loadUserInfo = async () => {
  try {
    const response = await http.get<User>(API_ENDPOINTS.AUTH.PROFILE)
    userInfo.value = response
    
    // 更新表单数据
    basicForm.username = response.username
    basicForm.email = response.email
    basicForm.avatar = response.avatar || ''
    
  } catch (error) {
    console.error('Failed to load user info:', error)
    ElMessage.error('加载用户信息失败')
  }
}

// 加载统计数据
const loadStats = async () => {
  try {
    // 这里应该调用实际的API获取用户统计数据
    // const response = await http.get('/users/stats')
    
    // 模拟数据
    stats.value = {
      recordCount: 156,
      fileCount: 89,
      loginCount: 234
    }
  } catch (error) {
    console.error('Failed to load stats:', error)
  }
}

// 头像上传处理
const beforeAvatarUpload = (file: File) => {
  const isImage = file.type.startsWith('image/')
  const isLt2M = file.size / 1024 / 1024 < 2
  
  if (!isImage) {
    ElMessage.error('只能上传图片文件')
    return false
  }
  
  if (!isLt2M) {
    ElMessage.error('图片大小不能超过 2MB')
    return false
  }
  
  return true
}

const handleAvatarSuccess = (response: any) => {
  basicForm.avatar = response.file.path
  ElMessage.success('头像上传成功')
}

const handleAvatarError = (error: any) => {
  console.error('Avatar upload failed:', error)
  ElMessage.error('头像上传失败')
}

// 更新基本信息
const handleUpdateBasic = async () => {
  if (!basicFormRef.value) return
  
  try {
    await basicFormRef.value.validate()
    
    saving.value = true
    
    await authStore.updateProfile({
      email: basicForm.email,
      avatar: basicForm.avatar
    })
    
    ElMessage.success('个人信息更新成功')
    await loadUserInfo()
    
  } catch (error: any) {
    console.error('Update profile failed:', error)
    ElMessage.error(error.message || '更新失败')
  } finally {
    saving.value = false
  }
}

// 修改密码
const handleChangePassword = async () => {
  if (!passwordFormRef.value) return
  
  try {
    await passwordFormRef.value.validate()
    
    saving.value = true
    
    await authStore.changePassword({
      oldPassword: passwordForm.oldPassword,
      newPassword: passwordForm.newPassword
    })
    
    ElMessage.success('密码修改成功')
    
    // 清空表单
    Object.assign(passwordForm, {
      oldPassword: '',
      newPassword: '',
      confirmPassword: ''
    })
    
  } catch (error: any) {
    console.error('Change password failed:', error)
    ElMessage.error(error.message || '密码修改失败')
  } finally {
    saving.value = false
  }
}

// 安全设置变更
const handleSecurityChange = () => {
  // 这里可以调用API保存安全设置
  ElMessage.success('安全设置已更新')
}

// 生命周期
onMounted(() => {
  loadUserInfo()
  loadStats()
})
</script>

<style scoped>
.profile {
  padding: 0;
}

.profile-card {
  text-align: center;
}

.profile-header {
  padding: 20px 0;
  border-bottom: 1px solid #ebeef5;
  margin-bottom: 20px;
}

.profile-header h3 {
  margin: 16px 0 8px 0;
  font-size: 20px;
  color: #303133;
}

.profile-header p {
  margin: 0 0 16px 0;
  color: #909399;
  font-size: 14px;
}

.profile-roles {
  display: flex;
  gap: 8px;
  justify-content: center;
  flex-wrap: wrap;
}

.profile-stats {
  display: flex;
  justify-content: space-around;
  padding: 20px 0;
}

.stat-item {
  text-align: center;
}

.stat-value {
  font-size: 24px;
  font-weight: bold;
  color: #409eff;
  line-height: 1;
}

.stat-label {
  font-size: 12px;
  color: #909399;
  margin-top: 4px;
}

.form-tip {
  font-size: 12px;
  color: #909399;
  margin-top: 4px;
}

.avatar-tip {
  font-size: 12px;
  color: #909399;
  margin-top: 8px;
  text-align: center;
}

.security-settings {
  padding: 20px 0;
}

.security-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  border-bottom: 1px solid #ebeef5;
}

.security-item:last-child {
  border-bottom: none;
}

.security-info h4 {
  margin: 0 0 4px 0;
  font-size: 16px;
  color: #303133;
}

.security-info p {
  margin: 0;
  font-size: 14px;
  color: #909399;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .profile-stats {
    flex-direction: column;
    gap: 16px;
  }
  
  .security-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }
}
</style>